-size: 16px;\n                    }\n                }\n                .goodsInfo {\n                    justify-content: flex-start;\n                    span:first-child {\n                        height: 140px;\n                        width: 210px;\n                        img {\n                            width: 100%;\n                        }\n                    }\n                    span:last-child {\n                        color: #333;\n                        font-size: 16px;\n                        width: 180px;\n                        margin-top: 10px;\n                        margin-left: 16px;\n                    }\n                }\n                .price {\n                    span {\n                        line-height: 30px;\n                    }\n                    span:first-child {\n                        font-weight: bold;\n                        font-size: 24px;\n                        color: #fc583d;\n                    }\n                    span:last-child {\n                        color: #999;\n                        text-decoration: line-through;\n                        font-size: 14px;\n                        margin-left: 10px;\n                    }\n                }\n            }\n        }\n    }\n    .options {\n        padding: 50px 10px 200px 10px;\n        button {\n            background-color: inherit;\n            border-radius: 2px;\n            padding: 0;\n            font-size: 16px;\n            outline: none;\n            border: none;\n        }\n        .circle {\n            display: inline-block;\n            height: 14px;\n            width: 14px;\n            border: 1px solid #666;\n            border-radius: 50%;\n            position: relative;\n        }\n        .circleActive {\n            &:after {\n                content: \"\";\n                position: absolute;\n                height: 6px;\n                width: 6px;\n                background-color: #999;\n                border-